:root {
  --void-bg: #FFFFFF;
  --void-bg-secondary: #f4f4f5;
  --void-bg-tertiary: #e4e4e7;
  --void-safety-orange: #d12424;
  --void-safety-orange-dim: #d12424cc;
  --void-white: #000000;
  --void-white-dim: #00000080;
  --void-white-faint: #00000020;
  --void-white-ghost: #00000010;
  --void-border: #00000015;
  --void-border-accent: #d1242430;
  --background: #FFFFFF;
  --foreground: #000000;
  --accent: #d12424;
  --grid-color: rgba(209, 36, 36, 0.03);
}
@font-face {
  font-family: 'Geist Mono';
  src: url('https://cdn.jsdelivr.net/npm/geist@1.3.0/dist/fonts/geist-mono/GeistMono-Variable.woff2') format('woff2');
  font-weight: 100 900;
  font-style: normal;
  font-display: swap;
}
html {
  background-color: var(--void-bg);
}
body {
  background-color: var(--void-bg);
  color: var(--void-white);
  font-family: var(--font-inter);
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scrollbar-width: thin;
  scrollbar-color: var(--void-safety-orange) var(--void-bg-secondary);
}
body::-webkit-scrollbar {
  width: 6px;
}
body::-webkit-scrollbar-track {
  background: var(--void-bg-secondary);
}
body::-webkit-scrollbar-thumb {
  background: var(--void-safety-orange);
  border-radius: 0;
}
.font-data {
  font-family: var(--font-geist-mono);
}
.font-ui {
  font-family: var(--font-inter);
}
::selection {
  background-color: var(--void-safety-orange);
  color: var(--void-white);
}
.void-border {
  border: 1px solid var(--void-border);
  border-radius: 0;
}
.void-border-accent {
  border: 1px solid var(--void-border-accent);
  border-radius: 0;
}
.void-grid {
  background-image: linear-gradient(rgba(209, 36, 36, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(209, 36, 36, 0.03) 1px, transparent 1px);
  background-size: 50px 50px;
}
.void-grid-dense {
  background-image: linear-gradient(rgba(209, 36, 36, 0.05) 1px, transparent 1px),
    linear-gradient(90deg, rgba(209, 36, 36, 0.05) 1px, transparent 1px);
  background-size: 10px 10px;
}
.void-scanlines {
  position: relative;
}
.void-scanlines::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: repeating-linear-gradient(0deg,
      transparent,
      transparent 2px,
      rgba(0, 0, 0, 0.3) 2px,
      rgba(0, 0, 0, 0.3) 4px);
  pointer-events: none;
  z-index: 10;
}
@keyframes flicker {
  0%,
  100% {
    opacity: 0.97;
  }
  50% {
    opacity: 1;
  }
}
.void-flicker {
  animation: flicker 0.15s infinite;
}
@keyframes blink {
  0%,
  50% {
    opacity: 1;
  }
  51%,
  100% {
    opacity: 0;
  }
}
.void-cursor {
  display: inline-block;
  width: 12px;
  height: 1.2em;
  background-color: var(--void-safety-orange);
  animation: blink 1s step-end infinite;
  vertical-align: text-bottom;
  margin-left: 2px;
}
.void-glitch {
  position: relative;
}
.void-input {
  background: transparent;
  border: none;
  border-bottom: 2px solid var(--void-border);
  color: var(--void-white);
  font-family: var(--font-geist-mono);
  font-size: clamp(1.5rem, 5vw, 4rem);
  outline: none;
  width: 100%;
  padding: 1rem 0;
  transition: border-color 0.3s ease;
}
.void-input:focus {
  border-color: var(--void-safety-orange);
}
.void-input::placeholder {
  color: var(--void-white-dim);
  opacity: 0.3;
}
.void-data-grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 1px;
  background-color: var(--void-border);
}
.void-data-cell {
  background-color: var(--void-bg);
  padding: 1rem;
  font-family: var(--font-geist-mono);
  font-size: 0.75rem;
  border-radius: 0;
}
.void-status-active {
  color: var(--void-safety-orange);
}
.void-status-ok {
  color: #00FF41;
}
.void-status-warn {
  color: #FFB800;
}
.void-status-error {
  color: #FF2E00;
}
.kinetic-grid {
  display: none;
}
.pixel-flow {
  display: none;
}
@keyframes grid-drift {
  0% {
    background-position: 0% 0%;
  }
  100% {
    background-position: 100% 100%;
  }
}
.drift-animate {
  animation: grid-drift 120s linear infinite;
}
.void-glow {
  box-shadow: 0 0 30px rgba(209, 36, 36, 0.3), 0 0 60px rgba(209, 36, 36, 0.1);
}
.void-glow-text {
  text-shadow: 0 0 20px rgba(209, 36, 36, 0.5);
}
@media (max-width: 768px) {
  html {
    font-size: 14px;
  }
  .void-grid {
    background-size: 25px 25px;
  }
}
@media (max-width: 768px) {
  * {
    -webkit-tap-highlight-color: transparent;
  }
  .touch-target {
    min-height: 44px;
    min-width: 44px;
  }
}
@keyframes gradient-flow {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.animate-gradient-flow {
  background-size: 200% 200%;
  background-position: 0% 50%;
  animation: gradient-flow 6s ease infinite;
}
@keyframes pulse-dramatic {
  0%,
  100% {
    opacity: 0.3;
    transform: scale(1.4) translateZ(0);
    filter: blur(100px) brightness(0.8) contrast(1.2);
  }
  50% {
    opacity: 1;
    transform: scale(2.2) translateZ(0);
    filter: blur(20px) brightness(1.8) contrast(1.5);
  }
}
.animate-pulse-dramatic {
  animation: pulse-dramatic 1.5s cubic-bezier(0.45, 0.05, 0.55, 0.95) infinite;
}
@keyframes grid-move {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(50px);
  }
}
.void-grid-animate {
  background-image: linear-gradient(var(--void-border-accent) 1px, transparent 1px),
    linear-gradient(90deg, var(--void-border-accent) 1px, transparent 1px);
  background-size: 50px 50px;
  animation: grid-move 4s linear infinite;
}
